body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'volkhovbold';
  src: url('./fonts/volkhov-bold-webfont.woff2') format('woff2'),
          url('./fonts/volkhov-bold-webfont.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'volkhovregular';
  src: url('./fonts/volkhov-regular-webfont.woff2') format('woff2'),
          url('./fonts/volkhov-regular-webfont.woff') format('woff');
  font-weight: normal;
  font-style: normal;

}


